.customize-control.customize-control-otf-slider{
    margin-bottom: 20px;
    .otf-slider{
        background-color: #fff;
        margin-top: 20px;
        width: calc(100% - 40px);
        .ui-slider-range{
            background: $main-color;
        }
    }

    .ui-slider-handle{
        span{
            position: absolute;
            -ms-transform: translate(-50%, -100%); /* IE 9 */
            -webkit-transform: translate(-50%, -100%); /* Safari */
            transform: translate(-50%, -100%);
            top: 0;
            left: 50%;
            text-align: center;
            display: block;
            width: 50px;
            font-size: 11px;
        }
    }

    .otf-customize-slider{
        position: relative;
    }

    .otf-reset{
        position: absolute;
        right: 0;
        top: 50%;
        -ms-transform: translate(0, -50%); /* IE 9 */
        -webkit-transform: translate(500, -50%); /* Safari */
        transform: translate(0, -50%);
        color: $text-color;
        &:hover{
            color: $main-color;
        }
    }
}