.otf-button-group{
    display: block;
    margin-bottom: 5px;
    .button{
        -webkit-border-radius: 0;
        border-radius: 0;
        -webkit-box-shadow: none;
        box-shadow: none;
        height: 40px!important;
        line-height: 37px!important;
        margin: 0 -1px 0 0!important;
        padding: 0 10px 1px!important;
        position: relative;
        min-width: 40px;
        text-align: center;
    }
    input[type=radio]:checked~.button {
        background: $main-color;
        border-color: darken($main-color, 10%);
        -webkit-box-shadow: inset 0 2px 5px -3px rgba($main-color, .5);
        box-shadow: inset 0 2px 5px -3px rgba($main-color, .5);
        color: #fff;
        z-index: 1;
    }
}