.opal-customfield-tabs{
    .cmb-type-oembed{
        .cmb-spinner{
            &[style]{
                visibility: visible!important;    
            }
            
            float: left;
        }

        .cmb2-media-status{
            .embed-status{
                position: relative;
                padding-bottom: 56.25%; /* 16:9 */
                padding-top: 25px;
                height: 0;
                width: 100%;
                iframe{
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%!important;
                    height: 100%!important;
                }
            }
        }
    }
}