.opal-onoffswitch {
    position: relative;
    width: 80px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.onoffswitch-checkbox {
    display: none!important;
    //+ .cmb2-metabox-description{
    //    display: none;
    //}
}

.onoffswitch-label {
    display: block;
    overflow: hidden;
    cursor: pointer;
    border: 1px solid $border-color;
    border-radius: 20px;
}

.onoffswitch-inner {
    display: block;
    width: 200%;
    margin-left: -100%;
    transition: margin 0.3s ease-in 0s;
    &:before, &:after {
        display: block;
        float: left;
        width: 50%;
        height: 30px;
        padding: 0;
        line-height: 31px;
        font-size: 12px;
        color: white;
        //font-family: Trebuchet, Arial, sans-serif;
        //font-weight: bold;
        box-sizing: border-box;
    }
    &:before {
        content: attr(data-yes);
        padding-left: 10px;
        background-color: $brand-primary;
        color: #FFFFFF;
    }
    &:after {
        content: attr(data-no);
        padding-right: 10px;
        background-color: $brand-danger;
        color: $border-color;
        text-align: right;
    }
}

.onoffswitch-switch {
    display: block;
    width: 15px;
    margin: 7.5px;
    background: #FFFFFF;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 46px;
    border: 1px solid $border-color;
    border-radius: 20px;
    transition: all 0.3s ease-in 0s;
}

.onoffswitch-checkbox:checked + .onoffswitch-label {
    .onoffswitch-inner {
        margin-left: 0;
    }
    .onoffswitch-switch {
        right: 0px;
    }
}