.opal-demo-theme-wrap {
    margin-top : 30px;
    .theme-screenshot {
        background : #fff;
        &:after{
            padding-top: 74.9% !important;
        }
    }
    .theme-actions {
        opacity : 1 !important;
    }
    .plugin-info{
        position: absolute;
        top: 77%;
        width: 100%;
        font-size: 14px;
        text-align: center;
    }

    .plugin-required {
        background: rgba(213, 78, 33, .95);
        text-transform: uppercase;
        color: #fff;
        padding: 5px;
        position: absolute;
        top: 85px;
    }
}

.opal-wrap {
    .dashicons-before {
        &:before {
            width  : auto !important;
            height : auto !important;
            font-size: inherit;
        }
    }
}