.customize-control {
    &.customize-control-otf-image-select {

    }
    .change-image {
        text-align: right;
        margin-top: 10px;
    }
    .button-change-image {
        &:before {
            content: "\f464";
            display: inline-block;
            position: relative;
            left: -2px;
            top: -2px;
            font: 400 20px/1 dashicons;
            vertical-align: middle;
            -webkit-transition: all .2s;
            transition: all .2s;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }
    }
    .image-select {
        > * {
            display: none;
        }

        .active {
            display: block;
        }
    }

    [otf-select-image-control]{
        .select-control {
            display: none;
        }
    }
    .opal-control-image-select {
        .select-control {
            display : none;
        }
    }
}

ul.select-list-image {
    @extend .d-flex;
    @extend .flex-wrap;
    li {
        display: block;
        margin-right: 10px;
        &:hover,
        &.active {
            .box {
                border-color: $main-color;
                box-shadow: 0 0 0 1px $main-color;
            }
        }
    }

    .box {
        border: 1px solid $border-color;

        img {
            display: block;
        }
    }
}

// Panel Change Image
#select-image-left {
    padding-top: 10px;
}

#available-select-image {
    position: absolute;
    top: 0;
    bottom: 0;
    left: -301px;
    visibility: hidden;
    overflow-x: hidden;
    overflow-y: auto;
    width: 300px;
    margin: 0;
    z-index: 4;
    background: #eee;
    -webkit-transition: left .18s;
    transition: left .18s;
    border-right: 1px solid #ddd;

}

#available-images-list {
    position: absolute;
    overflow: auto;
    top: 0;
    width: 100%;
    border-top: 1px solid #ddd;
    .image-select-tpl {
        position: relative;
        padding: 20px;
        background: #fff;
        border-bottom: 1px solid #ddd;
        border-left: 4px solid #fff;
        -webkit-transition: .15s color ease-in-out, .15s background-color ease-in-out, .15s border-color ease-in-out;
        transition: .15s color ease-in-out, .15s background-color ease-in-out, .15s border-color ease-in-out;
        cursor: pointer;
        &.active,
        &:hover {
            background: #f3f3f5;
            border-bottom-color: #ccc;
            color: #0073aa;
            border-left: 4px solid #0073aa;
        }
    }
    img {
        max-width: 100%;
    }
}

body.changing-image {
    #available-select-image {
        left: 0;
        visibility: visible;
    }
    #customize-preview {
        opacity: .4;
    }
    .wp-full-overlay-main {
        left: 300px;
    }
}

