/* Tab Settings */
#opaltab-customfield {
    width: 100%;
    float: left;
    overflow: hidden;
    border: 1px solid #ededed;
    margin-bottom: 20px;
    .opaltab-heading{
        background: #ffffff;
    }
    .inside{
        margin: 0;
    }
    .cmb-th, .cmb-td{
        //padding: 10px 0;
    }

    .cmb-td{
        //max-width: 570px;
    }

    .cmb-th{
        label{
            font-weight: 400;
            font-size: 13px;
        }
    }

    .cmb-repeatable-group{
        .cmb-row:last-child{
            border: 0;
        }
        .cmb-type-file{
            .cmb2-upload-button{
                float: none;
            }
        }
    }

    .cmb-row{
        border-bottom: 1px solid $border-color;
        padding: 0 1em;
    }

    .cmb-type-checkbox .cmb-td{
        width: auto;
    }

    .cmb-type-group{
        .cmb-row{
            margin-top: 15px;
            .cmb-nested{
                .cmb-row{
                    border: 0;
                }
            }
            .cmb-th{
                padding: 5px 0;
            }
            .cmb-remove-field-row{
                margin: 0;
                padding: 15px 0;
                border: 0;
            }
        }
    }
}
/* End Tab Settings */