.otf-font-style-group{
    label{
        float: left;
        width: 38px;
        height: 38px;
        text-align: center;
        display: block;
        position: relative;
        margin-right: 10px;
    }
    span{
        line-height: 38px;
    }
    input[type="checkbox"]{
        display: none;
        &:checked{
            + .otf-box{
                background: $main-color;
                border-color: $main-color;
                color: #fff;
                box-shadow: 0 0 0 1px #fff inset;
            }
        }
    }
    .otf-box{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #fff;
        border: 1px solid $border-color;
        color: $text-color;
        &:hover{
            box-shadow: 0 0 0 1px #fff inset;
            background: darken($border-color, 5%);

        }
    }
}