.select-control{
    position: relative;
    overflow: hidden;
    select{
        height: 36px;
        padding: 0 20px 0 10px;
        width: 100%;
        box-shadow: none;
        font-size: 12px;
        margin: 0;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        border: 1px solid #ddd;
        &:empty{
            display: none;
        }
    }
    &:after{
        content: '';
        border-color: #888 transparent transparent transparent;
        border-style: solid;
        border-width: 5px 4px 0 4px;
        height: 0;
        right: 7px;
        margin-top: -2px;
        position: absolute;
        top: 50%;
        width: 0;
    }
}

.customize-control-title{
    margin-top: 5px;
}


.otf-select-group-button{
    .button{
        margin-top: 10px;
        float: right;
    }
}