.wp-customizer {
    .select2_custom-container--open {
        z-index: 999999;
    }
    .select2_custom-results__options {
        li {
            margin-bottom: 0;
            position: relative;
        }
        .select2_custom-results__option {
            line-height: 28px;
            height: 28px;
        }
    }

    .select2_custom-selection__rendered {
        padding-bottom: 6px;
        padding-top: 6px;
    }

    .select2_custom-container{
        margin-bottom: 10px;
        width: 100%!important;
        .select2_custom-selection--single {
            border-radius: 0;
            height: 40px;
            .select2_custom-selection__arrow {
                height: 40px;
            }
        }
    }
    .select2_custom-container--open .select2_custom-dropdown--above {
        border-radius: 0;
    }
    .google-font-extend{
        @include clearfix;
        .select-control{
            float: left;
            width: calc(50% - 5px);
            &:first-child{
                margin-right: 10px;
            }
        }
    }
}

.otf-font {
    background-image: url('#{$path-image}customize/googlefonts.png');
    background-repeat: no-repeat;
    background-position-x: 15px;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 50%;
    background-position-y: 0;
}

