.customize-control{
    &.customize-control-heading{
        background: #333;
        color: #fff;
        margin-left: -13px;
        margin-right: -13px;
        width: calc(100% + 26px);
        padding: 0 15px;
        text-transform: uppercase;
        h4{
            letter-spacing: 2px;
            font-weight: 400;
            font-size: 11px;
        }
        &:nth-child(2){
            margin-top: -16px;
        }
    }
}