.opal-customfield-tabs {
    position: relative;
    overflow: hidden;
    margin: 0 auto;
    display: block;
    width: 100%;
    font-weight: 300;
    font-size: 1.25em;
    background: #fff;
    border: 1px solid $border-color;
    nav.opal-nav {

    }

    .select2-container{
        min-width: 200px;
    }

    /* Nav */
    .opal-content-wrap {
        float: left;
        width: calc(100% - 180px);
        position: relative;
        background: #fff;
    }
    .opal-content-wrap section {
        display: none;
        margin: 0 auto;
        &.content-current {
            display: block;
        }
    }

    @media screen and (max-width: 58em) {
        .tabs nav a {
            &.icon span {
                display: none;
            }
            &:before {
                margin-right: 0;
            }
        }
    }

    /* Individual tab styles */

    /*****************************/
    /* Bar */
    /*****************************/

    &.tabs-style-bar nav {
        background: rgba(40, 44, 42, 0.05);
        &.opal-nav{
            float: left;
            width: 180px;
            &:after {
                content: "";
                display: block;
                width: 180px;
                height: 9999em;
                position: absolute;
                bottom: -9999em;
                left: 0;
                top: 0;
                background-color: #fafafa;
            }
            ul {
                position: relative;
                display: block;
                margin: 0 auto;
                padding: 0;
                max-width: 1200px;
                list-style: none;
                li {
                    position: relative;
                    z-index: 1;
                    display: block;
                    margin: 0;
                }
            }
            a {
                position: relative;
                height: 40px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                line-height: 2.5;
                padding: 0 10px;
                span {
                    vertical-align: middle;
                    font-size: 0.75em;
                }
                //> * {
                //    display: flex;
                //}
            }
            li.tab-current a {
                color: #74777b;
            }
            a:focus {
                outline: none;
                -webkit-box-shadow: none;
                -moz-box-shadow: none;
                box-shadow: none;
            }
            ul {
                li {
                    a {
                        background-color: #f7f7f7;
                        color: #74777b;
                        transition: background-color 0.2s, color 0.2s;
                        text-decoration: none;
                        border-bottom: 1px solid $border-color;
                        display: flex;
                        align-items: center;
                        flex-direction: row;
                        &:hover, &:focus {
                            color: $brand-primary;
                        }
                        i {
                            font-size: 18px;
                            //vertical-align: middle;
                            margin-right: 10px;
                        }
                        span {
                            letter-spacing: 1px;
                            font-weight: 500;
                            font-size: 0.6em;
                            text-transform: uppercase;
                        }
                    }
                    &.tab-current a {
                        background: $brand-primary;
                        color: #fff;
                    }
                }
            }
        }

    }

    /*****************************/
    /* Bar 1 */
    /*****************************/
    &.tabs-style-bar-1{
        margin-top: 10px;
        background: rgba(40, 44, 42, 0.05);
        border: 0;
        .opal-content-wrap{
            width: 100%;
            float: none;
            padding: 15px;
            border: 0;
            background: #f1f1f1;
        }
        .opal-nav {
            >ul {
                border: 4px solid transparent;
                position: relative;
                display: -ms-flexbox;
                display: -webkit-flex;
                display: -moz-flex;
                display: -ms-flex;
                display: flex;
                margin: 0 auto;
                padding: 0;
                list-style: none;
                -webkit-flex-flow: row wrap;
                flex-flow: row wrap;
                -webkit-justify-content: center;
                justify-content: center;
                li {
                    position: relative;
                    z-index: 1;
                    display: block;
                    margin: 0;
                    text-align: center;
                    -webkit-flex: 1;
                    -moz-flex: 1;
                    -ms-flex: 1;
                    flex: 1;
                    a {
                        margin: 0 2px;
                        background-color: #f7f7f7;
                        color: #74777b;
                        transition: background-color 0.2s, color 0.2s;
                        &:hover, &:focus {
                            color: $brand-primary;
                            outline: none;
                        }
                        span {
                            text-transform: uppercase;
                            letter-spacing: 1px;
                            font-weight: 500;
                            font-size: 0.6em;
                        }
                        position: relative;
                        display: block;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        line-height: 2.5;
                        text-decoration: none;
                        font-size: 20px;
                        span {
                            vertical-align: middle;
                        }
                    }
                    &.tab-current a {
                        background: $brand-primary;
                        color: #fff;
                    }
                }
            }
        }
    }
}
